import { call, put, takeEvery, all } from "redux-saga/effects";
import axios from "axios";
import { success, failed } from "./lyrics";

const GET_ASYNCDATA = "TEST"; // 이 액션으로 체킹해서 호출

export const onGetAsyncData = () => ({ type: GET_ASYNCDATA });

// dispatch => checking =>
function* getAsyncData(action) {
  console.log("호출성공", action);
  try {
    /*  const { res, e } = yield call(
      axios
        .put("http://localhost:3001/api/insert")
        .then(res => res)
        .catch(e => e)
    ); */
    yield put(success());
  } catch (error) {
    console.error(error);
    yield put(failed());
  }
}

function* testSaga() {
  yield takeEvery(GET_ASYNCDATA, getAsyncData);
}

export function* rootSaga() {
  yield all([testSaga()]);
}

export default rootSaga;
